import React from "react";
import "./styles.css";

import { Formik, Field, Form } from "formik";

class InputField extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      strength: "not yet entered",
    };
  }

  checkPassword(pass) {
    let strength;
    let sameCaseRegex = /(^[a-z]*$)|(^[A-Z]*$)/g.test(pass);
    let symbolRegex = /.*[?!"£$%^&*|/\\\][(){}<>,.:;#~`¬=_+\-@'].*/g.test(pass);

    //POINTS BASED SYSTEM INSTEAD??

    if (pass.length < 8) strength = "very weak";
    else if (sameCaseRegex) strength = "weak";
    else if (!symbolRegex) strength = "strong";
    else strength = "very strong";

    this.setState({ strength });
  }

  handleChangeText(e) {
    this.setState({ password: e.target.value });
  }

  setBG() {
    switch (this.state.strength) {
      case "very weak":
        return "#A00";
      case "weak":
        return "#F55";
      case "strong":
        return "#FF6";
      case "very strong":
        return "#3A3";
      default:
        return "#888";
    }
  }

  render() {
    console.log(this.state.strength);
    return (
      <div className="passContainer" style={{ backgroundColor: this.setBG() }}>
        <Formik
          initialValues={{ password: "" }}
          onSubmit={(obj) => this.checkPassword(obj.password)}
        >
          <Form className="form">
            <label>Enter your password: </label>
            <Field id="password" name="password" placeholder="Password" />
            <br />
            <button type="submit">Check</button>
          </Form>
        </Formik>
        <h1>Your password is {this.state.strength}</h1>
      </div>
    );
  }
}
/*<div>
<form onSubmit={this.handleSubmit}>
  <label>Enter your password: </label>
  <input type="text" name="password" onChange={this.handleChangeText} />
  <br />
  <input type="submit" value="Check Password" name="Submit" />
</form>
</div>*/

export default InputField;
